	import hexSha1 from 'hex-sha1';
	import uniLoadMore from "@/components/uni-load-more.vue"
	import {ChangePageTitle} from '../../title.js'
	
	export default {
		data() {
			return {
				title: '联系人',
				back1: true,
				accessToken: '64e9275246c6545a3b5d3ad060fe4c516f44d556',
				appsercert: 'MTExMTEx',
				sha: '',
				keys: [],
				page: 1,
				status: 'more',
				isShow: false,
			}
		},
		components: {
			uniLoadMore
		},
		methods: {
			getlist() {
				var signStr =
					"access-token=" +
					this.accessToken +
					"&appsercert=" +
					this.appsercert
// 					 +
// 					"&page=" +
// 					this.page;
				console.log(signStr)
				this.sha = hexSha1(signStr);
				console.log(this.sha)
				this.$axios(this.$baseUrl.personlist,{
						'access-token': this.accessToken,
						sign: this.sha,
						// page: this.page
					})
					.then(res => {
						console.log(res)
						uni.hideLoading()
						this.keys = res.data.items
// 						if(res.data._links.self.href.substring(res.data._links.self.href.length-1)!=this.page){
// 							this.status = "noMore";
// 						} else {
// 							console.log(res.data);
// 							this.keys = this.keys.concat(res.data.items);
// 							this.isShow = true
// 						}
					}, (error) => {
						console.log(error)
					})
			},
			todetail(id){
				uni.navigateTo({
					url:'/pages/persondetail/index?id='+id
				})
			}

		},
		onReachBottom() {
			this.status = "loading";
			console.log("我被拉到底部了");
			setTimeout(() => {
				this.page++;
				console.log(this.page);
				this.getlist();
			}, 2000)
		},
		onLoad() {
			uni.showLoading({
				title: '加载中'
			})
			this.getlist()
		},
		onShow() {
			document.title = '联系人'
			ChangePageTitle('联系人')
		}
	}
